:root {
  --nwcGreen: hsl(167, 100%, 20.2%);
  --nwcLightGreen: hsl(120, 55.5%, 44.9%);
  --nwcRed: hsl(355, 84.5%, 54.5%);
  --nwcYellow: hsl(47, 100%, 46.1%);
  --nwcBlue: hsl(220, 100%, 23.1%);
  --nwcLightBlue: hsl(207, 100%, 40.6%);
  --black: hsl(0, 0%, 0%);
  --richBlack: hsl(0, 0%, 6%);
  --lightGrey: hsl(210, 1%, 60.8%);
  --grey: hsl(0, 0%, 50.2%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-text: "League Spartan", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-text);
}

.container {
  width: 100%;
  margin: 2.5rem auto 0;
}
@media (min-width: 64em) {
  .container {
    max-width: 87.5rem;
    width: 80%;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

body {
  background-image: url("../img/background_mobile_430x931-min.jpg");
  background-size: cover;
  height: 100vh;
  background-color: var(--white);
  transition: all 300ms ease-in-out;
}
@media (min-width: 40em) {
  body {
    background-image: url("../img/background_desktop_1920x1080-min.jpg");
    background-size: cover;
  }
}

section {
  margin-top: 2.5rem;
}

.heading {
  color: var(--white);
  font-size: 4.25rem;
  line-height: 0.9;
  text-transform: uppercase;
  text-align: center;
}